<template>
    <div id="root">
        <md-card v-for="img in imgs" :key="img" class="pic-card" md-with-hover>
            <md-ripple>
                <md-card-media-actions>
                    <md-card-media>
                        <img :src="imgMaps[img]" alt="Cover">
                    </md-card-media>
                    <md-card-actions>
                        <md-button
                        :class="['md-icon-button', theme]"
                        @click="setWallpaper(img)">
                            <md-icon>gavel</md-icon>
                        </md-button>
                    </md-card-actions>
                </md-card-media-actions>
            </md-ripple>
        </md-card>
    </div>
</template>

<script>
export default {
    name: 'gallary',
    props: {
        theme: String,
        rwConfig: Object,
        imgMaps: Object,
    },
    computed: {
        imgs () {
            return this.rwConfig.allImgs;
        }
    },
    methods: {
        setWallpaper (img) {
            if(img == this.rwConfig.currentBg) return;
            this.$emit('ipc-event', {request: 'set-bg', params: {currentBg: img}});
        }
    },
}
</script>

<style scoped>
    #root {
        width: 100%;
        height: 100%;
        padding: 20px;
        overflow-y: scroll;
    }
    .pic-card {
        width: 30%;
        display: inline-block;
        margin: 10px;
        border-radius: 5px;
    }
</style>